@import '_base.sass';

//Mobile up to 1000px
@media only screen and (max-width: 1000px) {
    header {
        @include theme($theme: $color-theme-Darkgreen);
        //display: flex
        //flex-flow: row
        width: 100vw;
        padding: 4vh 5vw 4vh 5vw;
        nav {
            ul {
                //display: flex
                //flex-flow: row
                li {
                    list-style-type: none;
                    a {
                        text-decoration: none; }
                    a:link {
                        color: $color-Darkgreen-font; }
                    a:visited {
                        color: $color-Darkgreen-font; }
                    a:hover {
                        color: #7A9983; } } } }
        .menuItem {
            display: block; }
        .hamburger {
            position: fixed;
            z-index: 5000;
            border: none;
            top: 1rem;
            rigth: 1rem;
            padding: 4px;
            cursor: pointer; }
        .closeOpt {
            display: none; }
        .globalNav {
            position: fixed;
            transform: translateY(-100%);
            transition: transform 0.2s;
            top: 0;
            left: 0;
            rigth: 0;
            bottom: 0;
            z-index: 4999;
            padding-top: 4rem; }

        //JS class
        .menuVisability {
            transform: translateY(0); } } }

